import React, { useState } from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import {
    Typography,
    FormControlLabel,
    Switch
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import * as Constants from "../common/Constants";
import { connect } from 'react-redux';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: "column",
        height: "100%",
        paddingLeft: "16px"
    },
    heading: {
        color: theme.palette.primary.textColor,
        width: "100%",
        fontWeight: 600,
        fontSize: "48px"
    },
    firstName: {
        color: theme.palette.primary.textColor,
        fontSize: "72px",
        fontWeight: 600
    },
    lastName: {
        color: theme.palette.primary.main,
        fontSize: "72px",
        fontWeight: 600
    },
    skillHeaderDiv: {
        paddingTop: "24px"
    },
    skillHeader: {
        fontSize: "24px",
        fontWeight: 600
    },
    skillBullets: {
        paddingTop: "24px",
        display: "flex",
        flexDirection: "column"
    },
    designation: {
        fontSize: "22px",
        fontWeight: 500
    }
}));

const Skills = (props) => {
    const classes = useStyles();
    const [themeType, setThemeType] = useState(props.darkMode ? "dark" : "light");

    return (
        <Typography component="div" className={classes.root}>
            <Typography component="div" className={classes.heading}>SKILLS</Typography>
            <Typography component="div" className={classes.skillHeaderDiv}>
                <Typography component="span" className={classes.skillHeader}>Programming Languages</Typography>
            </Typography>
            <Typography component="div" className={classes.skillBullets}>
                <Typography component="span"> - React JS</Typography>
                <Typography component="span"> - Javascript</Typography>
                <Typography component="span"> - Node JS</Typography>
                <Typography component="span"> - Core Java</Typography>
                <Typography component="span"> - Angular JS</Typography>
            </Typography>
        </Typography>
    );
}

const mapStateToProps = (state) => {
    return {
        darkMode: state.darkMode
    }
}

export default connect(mapStateToProps)(Skills);
